import classnames from "classnames";
import React from "react";
import PaginationButton from "./PaginationButton";
import Button from 'bee-button';
import Select from 'bee-select';

import PropTypes from "prop-types";
import i18n from './i18n';
import {getComponentLocale} from 'bee-locale/build/tool';


const Option = Select.Option;

const propTypes = {
    /**
     * 
     */
    activePage: PropTypes.number,
    /**
     * 
     */
    items: PropTypes.number,
    /**
     * 1maxButton
     */
    maxButtons: PropTypes.number,

    /**
     * true,
     */
    boundaryLinks: PropTypes.bool,

    /**
     * true,
     *
     */
    ellipsis: PropTypes.oneOfType([PropTypes.bool, PropTypes.node]),

    /**
     *  true,
     */
    first: PropTypes.oneOfType([PropTypes.bool, PropTypes.node]),

    /**
     *  true,
     */
    last: PropTypes.oneOfType([PropTypes.bool, PropTypes.node]),

    /**
     * true,
     */
    prev: PropTypes.oneOfType([PropTypes.bool, PropTypes.node]),

    /**
     * true,
     */
    next: PropTypes.oneOfType([PropTypes.bool, PropTypes.node]),

    /**
     * 
     */
    onSelect: PropTypes.func,

    /**
     * You can use a custom element for the buttons
     */
    buttonComponentClass: PropTypes.oneOfType([
        PropTypes.element,
        PropTypes.string
    ]),
    /**
     * 
     */
    dataNumSelect: PropTypes.array,
    /**
     * 
     */
    dataNum: PropTypes.number,
    /**
     * 
     */
    showJump: PropTypes.bool,
    /**
     * 
     */
    total: PropTypes.number
};

const defaultProps = {
    activePage: 1,
    maxButtons: 0,
    first: false,
    last: false,
    prev: false,
    next: false,
    ellipsis: true,
    boundaryLinks: false,
    clsPrefix: "u-pagination",
    gap: false,
    noBorder: false,
    dataNumSelect: [
         '5',
        '10',
        '15',
        '20'
    ],
    dataNum: 1,
    showJump: false,
    locale: {}
};


class Pagination extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            activePage: this.props.activePage,//
            dataNum: props.dataNum,
            items: props.items ? props.items : props.total ? Math.ceil(props.total / props.dataNumSelect[props.dataNum]) : 1,
            jumpPageState: ''
        }
    }

    componentWillReceiveProps(nextProps) {
        if (this.state.activePage !== nextProps.activePage) {
            this.setState({
                activePage: nextProps.activePage,
            })
        }
        if (nextProps.dataNum && this.props.dataNum !== nextProps.dataNum) {
            this.setState({
                dataNum: nextProps.dataNum,
                items: Math.ceil(nextProps.total/nextProps.dataNumSelect[nextProps.dataNum])
            })
        }
        if (nextProps.items && this.props.items !== nextProps.items) {
            this.setState({
                items: nextProps.items,
            })
        }
    }

    onKeyup = (e) => {
        e.keyCode === 13 && this.handleEnsurePageJump()
    }

    setPageJump = (e) => {
        let value = e.target.value;
        if( isNaN(Number(value)) || value > this.state.items || value <= 0){
            return false;
        }
        this.setState({
            jumpPageState: value
        })
    }

    /**
     * 
     */
    handleEnsurePageJump = () => {
        const {jumpPageState} = this.state;
        const { onSelect } = this.props;
        if(jumpPageState === ''){
            return;
        }
        this.setState({
            activePage: jumpPageState * 1,
            jumpPageState: ''
        });
        if(typeof onSelect === 'function'){
            onSelect(jumpPageState * 1)
        }
    }

    /**
     * 
     * @param e
     */
    dataNumSelect = (value) => {
        console.log(value);
        const { onDataNumSelect, total } = this.props;
        let dataNumValue = this.props.dataNumSelect[value];
        console.log("dataNumValue", dataNumValue);
        if(total){
            this.setState({
                items: Math.ceil(total/dataNumValue)
            })
        }
        this.setState({
            dataNum: value
        })
        if (typeof onDataNumSelect === 'function') {
            onDataNumSelect(value, dataNumValue)
        }
    }

    /**
     * page
     * @param activePage
     * @param items
     * @param maxButtons
     * @param boundaryLinks
     * @param ellipsis
     * @param buttonProps
     * @returns {Array}
     */
    renderPageButtons(activePage, items, maxButtons, boundaryLinks, ellipsis, buttonProps) {
        let pageButtons = [];

        let startPage;
        let endPage;
        let hasHiddenPagesAfter;

        if (maxButtons) {
            //maxactivepageactiveButton
            let hiddenPagesBefore = activePage - parseInt(maxButtons / 2, 10);
            startPage = hiddenPagesBefore > 2 ? hiddenPagesBefore : 1;
            //activeButton
            hasHiddenPagesAfter = startPage + maxButtons < items + 1;

            if (!hasHiddenPagesAfter) {
                endPage = items;
                startPage = items - maxButtons + 1;
                if (startPage < 1) {
                    startPage = 1;
                }
            } else {
                endPage = startPage + maxButtons - 1;
            }
        } else {
            startPage = 1;
            endPage = items;
        }
        //button
        for (let pagenumber = startPage; pagenumber <= endPage; pagenumber++) {
            pageButtons.push(
                <PaginationButton
                    {...buttonProps}
                    key={pagenumber}
                    eventKey={pagenumber}
                    active={pagenumber === activePage}
                >
                    {pagenumber}
                </PaginationButton>
            );
        }
        //boundaryLinkseclipsisstartPage!=1 before More Button
        if (boundaryLinks && ellipsis && startPage !== 1) {
            if (startPage > 2) {
                pageButtons.unshift(
                    <PaginationButton
                        key="ellipsisFirst"
                        disabled
                        componentClass={buttonProps.componentClass}
                    >
                    <span aria-label="More">
                      {ellipsis === true ? "\u2026" : ellipsis}
                    </span>
                    </PaginationButton>
                );
            }
            // Button
            pageButtons.unshift(
                <PaginationButton {...buttonProps} key={1} eventKey={1} active={false}>
                    1
                </PaginationButton>
            );
        }
        //maxButtonseclipsishasHiddenPagesAfter after More Button
        if (maxButtons && hasHiddenPagesAfter && ellipsis) {
            if ( !boundaryLinks || ( boundaryLinks && (items > 1 + endPage) ) ) {
                pageButtons.push(
                    <PaginationButton
                        key="ellipsis"
                        disabled
                        componentClass={buttonProps.componentClass}
                    >
                    <span aria-label="More">
                        {ellipsis === true ? "\u2026" : ellipsis}
                    </span>
                    </PaginationButton>
                );
            }
            //  true 
            if (boundaryLinks && endPage !== items) {
                pageButtons.push(
                    <PaginationButton
                        {...buttonProps}
                        key={items}
                        eventKey={items}
                        active={false}
                    >
                        {items}
                    </PaginationButton>
                );
            }
        }

        return pageButtons;
    }

    render() {
        const local = getComponentLocale(this.props, this.context, 'Pagination', () => i18n);

        const {
            items,
            maxButtons,
            boundaryLinks,
            ellipsis,
            first,
            last,
            prev,
            next,
            onSelect,
            buttonComponentClass,
            noBorder,
            className,
            clsPrefix,
            size,
            gap,
            onDataNumSelect,
            dataNumSelect,
            dataNum,
            activePage,
            showJump,
            total,
            ...others
        } = this.props;

        const activePageState = this.state.activePage;
        const jumpPageState = this.state.jumpPageState;
        const classes = {};
        if (noBorder) {
            classes[`${clsPrefix}-no-border`] = true;
        }
        if (size) {
            classes[`${clsPrefix}-${size}`] = true;
        }
        if (gap) {
            classes[`${clsPrefix}-gap`] = true;
        }

        let classNames = classnames(`${clsPrefix}-list`, classes);

        /**
         *  
         *  onSelect:Pagination
         *  componentClass: dom
         */
        const buttonProps = {
            onSelect,
            componentClass: buttonComponentClass
        };

        return (

            <div className={clsPrefix}>
                <ul {...others} className={classnames(className, classNames)}>
                    {first && (
                        <PaginationButton
                            {...buttonProps}
                            eventKey={1}
                            disabled={activePageState === 1}
                        >
                            <span aria-label="First">{first === true ? "\u00ab" : first}</span>
                        </PaginationButton>
                    )}
                    {prev && (
                        <PaginationButton
                            {...buttonProps}
                            eventKey={activePageState - 1}
                            disabled={activePageState === 1}
                        >
                            <span aria-label="Previous">{prev === true ? "\u2039" : prev}</span>
                        </PaginationButton>
                    )}

                    {this.renderPageButtons(
                        activePageState,
                        this.state.items,
                        maxButtons,
                        boundaryLinks,
                        ellipsis,
                        buttonProps
                    )}

                    {next && (
                        <PaginationButton
                            {...buttonProps}
                            eventKey={activePageState + 1}
                            disabled={activePageState >= this.state.items}>
                            <span aria-label="Next">{next === true ? "\u203a" : next}</span>
                        </PaginationButton>
                    )}
                    {last && (
                        <PaginationButton
                            {...buttonProps}
                            eventKey={this.state.items}
                            disabled={activePageState >= this.state.items}>
                            <span aria-label="Last">{last === true ? "\u00bb" : last}</span>
                        </PaginationButton>
                    )}
                </ul>
                {
                    total != null ? (
                        <div className={`${clsPrefix}-total`}>
                            {local['total']}
                            <span>{total}</span>
                            {local['items']}
                        </div>
                    ) : null
                }

                {
                    showJump ? (
                        <div className="data_per_select">
                            {local['show']}
                            {/* <select
                                name="data-select"
                                className="data_select"
                                value={this.state.dataNum}
                                onChange={this.dataNumSelect}>
                                {dataNumSelect.length > 0 &&
                                dataNumSelect.map((item, i) => {
                                    return <option key={i} value={i}>{item}</option>
                                })}
                            </select> */}
                            <Select
                                // className="data_select"
                                value={this.state.dataNum}
                                onChange={this.dataNumSelect}>
                                {dataNumSelect.length > 0 &&
                                dataNumSelect.map((item, i) => {
                                    return <Option key={i} value={i}>{item}</Option>
                                })}
                            </Select>
                            {local['items']}
                        </div>
                    ) : null
                }
                {
                    showJump ? (
                        <div className="page_jump">
                            {local['goto']}
                            <input
                                className="page_jump_value"
                                value={jumpPageState}
                                onKeyDown={this.onKeyup}
                                onChange={this.setPageJump}
                            />
                            {local['page']}
                            <Button
                                className="page_jump_btn"
                                onClick={this.handleEnsurePageJump}
                                shape="border">
                                {local['ok']}
                            </Button>
                        </div>
                    ) : null
                }
                
            </div>

        );
    }
}

Pagination.propTypes = propTypes;
Pagination.defaultProps = defaultProps;
Pagination.contextTypes = {
    beeLocale: PropTypes.object
};
export default Pagination;


